<template>
   <div class="collection" v-show="showcol" style="max-height:3.2rem;overflow:auto;" >
          <div class="collect" v-for="item in getcollect" style="width: 100%;height: 1.066667rem;display: flex;">
              <div class="avatar" style="flex: 0 0 1.066667rem;height: 1.066667rem;">
                <img :src="item.imgUrl" alt="" width="1.066667rem" height="1.066667rem">
              </div>
              <div class="content" style="flex: 1">
                 <div class="title">{{item.title}}</div>
                 <div>
                   <div>{{item.score}}</div>
                 </div>
              </div>
          </div>
      </div>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>

</style>
<script type="text/ecmascript-6">
import {mapGetters} from 'vuex'
export default {
   data () {
      return {
        showcol: false
      }
   },
   computed : {
      ...mapGetters([
         'getusername'
      ])
    }
}
</script>



