<template>
  <div class="person" >
      <div class="head" >
        <img :src="getusersrc" alt="">
        <p>{{getusername}}</p>
      </div>
      <div class="background" :style="'backgroundImage:url('+getheadimg+')'">
      </div>
      <div class="content">
         <div class="menu1" @click="changeImg">更换头像</div>
         <div class="menu" >修改密码</div>
         <div class="menu" @click="collectlist">我的收藏</div>
         <collection v-show="showcol"></collection>
         <div class="menu" @click="logout">退出应用</div>
      </div>
      <upload class="upclass"></upload>
  </div>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.person
   width: 100%
   height: 100%
   position: relative
  .head
    text-align: center
    height: 5.333333rem
    img
      width: 2.666667rem
      height: 2.666667rem
      border-radius: 50%
      margin-top: 0.666667rem
    p
      font-size: 0.666667rem

  .background
    width: 100%
    height: 5.333333rem
    z-index: -1
    position: absolute
    top: 0
    left: 0
  .content
    height: 11.12rem
    background: rgb(240,240,240)
    .menu
     height: 1.066667rem
     width: 90%
     margin: 0 auto
     border-radius: 0.066667rem
     background: #26A2FF
     text-align: center
     margin-top: 0.266667rem
     line-height: 1.066667rem
    .menu1
      height: 1.066667rem
      width: 90%
      margin: 0 auto
      border-radius: 0.066667rem
      background: #26A2FF
      text-align: center
      line-height: 1.066667rem
  .upclass
       position: absolute
       top: 50%
       left: 50%
       width: 9.0rem
       height: 5.333333rem
       margin-left: -4.5rem
       margin-top: -2.933333rem
       z-index: 20
       background: white



</style>
<script type="text/ecmascript-6" scoped>
  import upload from '../upload/upload'
  import collection from  '../collection/collection'
  import {mapGetters} from 'vuex'
  import Exif from 'exif-js'
  export default {
    data () {
      return {
        showcol: false,
        loading: false,
        percent: 0
      }
    },
    mounted () {
    },
    methods: {
      changeImg(){
         var url = 'static/pic/camera.png';
         this.$store.commit('setheaderImg',url);
         this.$store.commit('setheadImage');
      },
      collectlist () {
        this.showcol = !this.showcol;
        if(this.showcol){
           this.$store.commit('setcollect',JSON.parse(sessionStorage.collect));
        }
      },
      logout () {
          var data = {
            user : this.getusername
          };
          this.$store.dispatch('tologout',data);
          setTimeout(() => {
              this.$router.push({path:'/login'});
          },0);
      }
    },
    computed : {
      ...mapGetters([
         'getusername',
         'getusersrc',
         'getheadImage',
         'getheaderImg',
         'getcollect',
         'getheadimg'
      ])
    },
    components: {
      upload,
      collection
    }
  }
</script>



